
import { defineComponent, nextTick, reactive, ref, watch } from 'vue'
import draggable from 'vuedraggable'
import {
  AimOutlined,
  PlusSquareOutlined,
  BookOutlined,
  DeleteOutlined,
  PlusOutlined
} from '@ant-design/icons-vue'
import { message } from 'ant-design-vue'
import {
  MoreOne,
  Delete,
  Edit,
  ArrowUp,
  ArrowDown,
  Write
} from '@icon-park/vue-next'

interface List {
  name: string
  id: string
  children: { [key: string]: string | number | boolean }[]
}

export default defineComponent({
  name: 'Home',
  display: 'Handle',
  instruction: 'Drag using the handle icon',
  order: 5,
  components: {
    AimOutlined,
    // PieChartOutlined,
    PlusSquareOutlined,
    BookOutlined,
    DeleteOutlined,
    PlusOutlined,
    MoreOne,
    Delete,
    ArrowUp,
    ArrowDown,
    Edit,
    Write,
    draggable
  },
  setup() {
    const target_list = ref<List[]>([
      // {
      //   name: '目标1',
      //   id: Math.random().toString(36).substr(2),
      //   children: [
      //     {
      //       name: '指标1',
      //       schedule: 0,
      //       disbled: false,
      //       id: Math.random().toString(36).substr(2)
      //     }
      //   ]
      // }
    ])

    const del_index = reactive({ val: [], in: 0 })
    const editName = ref<string>('')
    const targerVal = ref<string>('')
    const isAdd = ref<boolean>(false)
    const title = ref<string>('')
    const isCreate = ref<boolean>(false)
    const Index = ref(999)
    const index1 = ref(999)
    const isDel = ref(false)
    const position = ref('')
    const drag = ref(false)
    // 编辑框聚焦
    const focusOn = (z: string, n: number) => {
      if (z === 'a') {
        isCreate.value = true
        nextTick(() => {
          const dom: any = document.getElementById('ip')
          dom.focus()
        })
      }
      if (z === 'b') {
        targerVal.value = ''
        Index.value = n
        isAdd.value = true
        nextTick(() => {
          const dom: any = document.getElementById('ix')
          dom.focus()
        })
      }
    }
    // 拖拽配置项
    const dragOptions = {
      animation: 200,
      group: 'description',
      disabled: false,
      ghostClass: 'ghost'
    }
    // 在上下方创建任务
    const createTask = (pos: string, val: [], i: number, i1: number) => {
      console.log(pos, val, i, i1)
      position.value = pos
      del_index.val = val
      del_index.in = i
      nextTick(() => {
        let dom
        pos === 'up' && (dom = document.getElementById('up') as HTMLElement)
        pos === 'down' && (dom = document.getElementById('down') as HTMLElement)
        dom && dom.focus()
      })
    }
    // 菜单栏消失
    const visibleTask = () => {
      const dom = document.querySelectorAll(
        '.ant-popover-placement-bottomLeft'
      ) as NodeListOf<HTMLElement>
      dom.forEach((v) => {
        v.style.display = 'none'
      })
    }
    // 重命名
    const rename = (i1: number, i2: number, name: string) => {
      console.log(i1, i2, name)
      editName.value = name
      Index.value = i1
      index1.value = i2
      visibleTask()
      const dom = document.querySelectorAll(
        '.target .index-title-input'
      ) as NodeListOf<HTMLElement>
      dom.forEach((v: any) => {
        if (v.id === 'a' + i1 + i2) {
          v.style.display = 'flex'
          v.firstElementChild.focus()
          v.previousElementSibling.setAttribute('style', 'display:none')
        }
      })
    }
    // 取消编辑
    const cancelEdit = (e: any, name: { [key: string]: string }, b = true) => {
      !b && (name.name = editName.value)
      console.log(name.name)
      e.target.parentNode.setAttribute('style', 'display:none')
      e.target.parentNode.previousElementSibling.setAttribute(
        'style',
        'display:flex'
      )
    }
    // 指标部分
    const isToggle = ref<boolean>(false)
    const delTar = (i: number) => {
      target_list.value.splice(i, 1)
      message.success('删除成功', 1)
    }
    // 删除指标
    const delIndex = (v: [], i: number) => {
      v.splice(i, 1)
      isDel.value = false
      message.success('删除成功', 1)
    }
    // 添加新目标或指标
    const addIndex = (
      item: List = {
        name: '',
        children: [],
        id: ''
      }
    ) => {
      if (position.value === 'up') {
        item.children.splice(del_index.in, 0, {
          name: targerVal.value !== '' ? targerVal.value : '新指标',
          schedule: 0,
          disbled: false,
          id: Math.random().toString(36).substr(2)
        })
        position.value = ''
        targerVal.value = ''
      }
      if (position.value === 'down') {
        item.children.splice(del_index.in + 1, 0, {
          name: targerVal.value !== '' ? targerVal.value : '新指标',
          schedule: 0,
          disbled: false,
          id: Math.random().toString(36).substr(2)
        })
        position.value = ''
        targerVal.value = ''
      }
      if (isAdd.value) {
        console.log('aaaaaaaa', item.children)
        targerVal.value &&
          item.children.push({
            name: targerVal.value,
            schedule: 0,
            disbled: false,
            id: Math.random().toString(36).substr(2)
          })
        targerVal.value ||
          item.children.push({
            name: '新指标',
            schedule: 0,
            disbled: false,
            id: Math.random().toString(36).substr(2)
          })
        isAdd.value = false
        targerVal.value = ''
      }
      if (isCreate.value) {
        title.value &&
          target_list.value.push({
            name: title.value,
            id: Math.random().toString(36).substr(2),
            children: []
          })
        title.value ||
          target_list.value.push({
            name: '新目标',
            id: Math.random().toString(36).substr(2),
            children: []
          })
        isCreate.value = false
      }
    }
    const toggleIndex = (val: [], i: number, order: string) => {
      order === 'on' && ([val[i], val[i - 1]] = [val[i - 1], val[i]])
      order === 'down' && ([val[i], val[i + 1]] = [val[i + 1], val[i]])
    }
    const edit = (i: number) => {
      target_list.value[i].children.forEach((element, i, arr) => {
        arr[i].disbled = true
      })
    }
    watch(isDel, (v) => {
      v && visibleTask()
    })

    return {
      target_list,
      isCreate,
      title,
      targerVal,
      delTar,
      isAdd,
      Index,
      index1,
      edit,
      delIndex,
      toggleIndex,
      isToggle,
      addIndex,
      focusOn,
      rename,
      cancelEdit,
      editName,
      isDel,
      del_index,
      position,
      createTask,
      dragOptions,
      drag
    }
  }
})
